.style_image {
  color: #FFF;
  font-size: 50px;
  background-color: rgba(0, 0, 0, 0);
  padding: 0px;
  border-radius: 5px 5px 5px 5px;
  -moz-border-radius: 5px 5px 5px 5px;
  -webkit-border-radius: 5px 5px 5px 5px;
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  }

  .style_image_home {
    position: relative;
    -webkit-animation: App-logo-home 2s infinite;
            animation: App-logo-home 2s infinite;
    -webkit-animation-direction:alternate;
            animation-direction:alternate;
    color: #FFF;
    font-size: 200px;
    background-color: rgba(0, 0, 0, 0);
    padding: 0px;
  }

  @-webkit-keyframes App-logo-home {
    from {
      left: -100px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    to {
      left: 100px;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
 
  }

  @keyframes App-logo-home {
    from {
      left: -100px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    to {
      left: 100px;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
 
  }
  .style_image_home_izq  {
    position: relative;
    -webkit-animation: App-logo-home-izq 2s infinite;
            animation: App-logo-home-izq 2s infinite;
    -webkit-animation-direction:alternate;
            animation-direction:alternate;
    color: #FFF;
    font-size: 200px;
    background-color: rgba(0, 0, 0, 0);
    padding: 0px;
  }

  @-webkit-keyframes App-logo-home-izq {
    from {
      left: 100px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    to {
      left: -100px;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
 
  }

  @keyframes App-logo-home-izq {
    from {
      left: 100px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    to {
      left: -100px;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
 
  }
  
  


  
